.form-check-label.active {
    background-color: rgba(98, 100, 104, 0.937);
    font-weight: 600;
    user-select: none;
    letter-spacing: 0.5px;
    border-radius: 10px;
    cursor: pointer;
  }
  .form-check-label.inactive {
    user-select: none;
    cursor: pointer;
    letter-spacing: 0.5px;
    opacity: 0.8;
    text-align: center;
    border-radius: 5px;
  }
  .inactive:hover {
    background-color: rgba(58, 52, 52, 0.937);
    opacity: 0.95;
  }
  .form-check-label {
    padding: 10px 15px;
    font-size: 18px;
    border: 2px solid rgb(77, 144, 90);
    margin: 10px;
  }
  
  .text {
    font-size: 17px;
    text-align: center;
  }
  
  @media screen and (max-width: 600px) {
    .form-check-label {
      margin: 5px;
    }
  }